<template>
  <div id="mian">
    <el-container style="width: 100%; height: 100%">
      <el-aside width="auto"
        ><!-- 菜单 -->
        <MenuList
          background-color="#2365a9"
          :Menus="Menus"
          :collapse="collapse"
          text-color=" #fff"
          active-text-color="#fff"
          class="menuList"
        ></MenuList
      ></el-aside>
      <el-container>
        <el-header
          style="
            background: #fff;
            margin: 0 10px;
            line-height: 50px;
            display: flex;
            align-items: center;
          "
          height="50px"
          ><!-- 头部 -->
          <appTop @menusCollapse="menusCollapse"></appTop
        ></el-header>
        <el-main
          style="background: #fff; height: calc(100vh - 70px); margin: 10px"
          ><!-- 路由显示 -->
          <router-view class="view"> </router-view
        ></el-main>
