<template>
  <div
    style="width: 100%; height: 100%; background-size: 100% 100%"
    class="loginbox"
  >
    <div class="login">
      <div class="form">
        <el-form :model="formInline" ref="myform">
          <p
            style="
              text-align: center;
              margin-bottom: 10px;
              font-size: 20px;
              font-weight: 600;
            "
          >
            登陆
          </p>
          <el-form-item
            label="账号"
            prop="user"
            :rules="[
              { required: true, message: '必须输入' },
              { type: 'string', message: '' },
            ]"
          >
            <el-input v-model="formInline.user" placeholder="请输入账号" />
          </el-form-item>
          <el-form-item
            prop="password"
            label="密码"
            :rules="[
              { required: true, message: '必须输入' },
              { type: 'string', message: '' },
            ]"
          >
            <el-input
              type="password"
              v-model="formInline.password"
              placeholder="请输入密码"
            />
          </el-form-item>
        </el-form>
        <div style="display: flex; justify-content: center">
          <el-button @click="login" type="primary">立即登陆</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { defineComponent, ref } from "vue";
import { ElMessage } from "element-plus";
import store from "../../store/index.ts";
export default defineComponent({
  name: "login",
  setup() {
    const formInline = ref({ user: "", password: "" });
    let myform = ref(null);
    const login = () => {
      console.log(myform);
      myform.value.validate((valid) => {
        if (valid) {
          const res = store.commit("login", formInline);
          console.log(res);
        } else {
          ElMessage.error("校验失败!");
          return false;
        }
      });
    };

    return { formInline, login, myform };
  },
});
</script>
<style lang="scss" scoped>
.form {
  width: 300px;
  padding: 10px 20px 20px 20px;
  background: #fff;
  position: fixed;
  top: calc(50% - 150px);
  left: calc(50% - 140px);
  border-radius: 5px;
}
.loginbox {
  background: red;
  background-image: url(../../assets/bgc.jpg);
}
</style>
