<template>
  <div class="top">
    <el-icon size="30" class="flod" @click="showMenusHandle"
      ><Fold v-if="collapse == false"></Fold><Expand v-else></Expand
    ></el-icon>
    <div
      style="
        display: flex;
        align-items: center;
        width: 200px;
        position: absolute;
        right: 30px;
      "
    >
      <img
        src="../assets/user.png"
        alt=""
        style="
          height: 40px;
          width: 40px;
          border: 1px solid #efefef;
          border-radius: 50%;
          margin-right: 10px;
        "
      />
      <span style="margin-right: 10px">{{ user.name }}</span>
      <el-popconfirm
        title="确定退出登陆吗?"
        @confirm="out"
        confirm-button-text="确定"
        cancel-button-text="取消"
      >
        <template #reference>
          <el-icon size="26"><SwitchButton /></el-icon>
        </template>
      </el-popconfirm>
    </div>
  </div>
</template>
<script>
import { defineComponent, ref } from "vue";
import router from "../router/index.ts";
export default defineComponent({
  setup(prop, { emit }) {
    const user = JSON.parse(window.localStorage.getItem("coderUser"));
    let collapse = ref(false);
    const showMenusHandle = () => {
      collapse.value = !collapse.value;
      emit("menusCollapse", collapse.value);
    };
    const out = () => {
      window.localStorage.removeItem("coderUser");
      window.localStorage.removeItem("coderMenus");
      window.localStorage.removeItem("codertoken");
      router.push("/login");
    };
    return { showMenusHandle, collapse, user, out };
  },
});
</script>
<style lang="scss">
.top {
  display: flex;
  align-items: center;
  .flod {
    cursor: pointer;
  }
}
.el-icon {
  cursor: pointer;
}
</style>
