import { createRouter, createWebHashHistory, RouteRecordRaw } from "vue-router";
// 药品管理;
import login from "../views/login/login.vue";
import main from "../views/main/main.vue";
import home from "../views/home/home.vue";
import medicineManage from "../views/medicineControl/index.vue";
import audit from "../views/medicineControl/audit.vue";
import deleteM from "../views/medicineControl/delete.vue";
import increase from "../views/medicineControl/increase.vue";
import Inventory from "../views/medicineControl/Inventory.vue";
import customControl from "../views/customControl/customControl.vue";
// 订单
import orderControl from "../views/orderControl/orderControl.vue";
const routes: Array<RouteRecordRaw> = [
  {
    path: "/main",
    component: main,
    children: [
      {
        path: "",
        component: home,
      },
      {
        path: "medicineControl",
        component: medicineManage,
        children: [
          {
            path: "audit",
            component: audit,
          },
          {
            path: "delete",
            component: deleteM,
          },
          // 新增
          {
            path: "increase",
            component: increase,
          },
          // 库存
          {
            path: "Inventory",
            component: Inventory,
          },
        ],
      },
      {
        path: "orderControl",
        component: orderControl,
      },
      {
        path: "customControl",
        component: customControl,
      },
    ],
  },
  {
    path: "/login",
    component: login,
  },
];

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
});
router.beforeEach((to) => {
  const token = window.localStorage.getItem("codertoken");
  // ...
  // 返回 false 以取消导航
  if (to.path != "/login") {
    if (!token) {
      return "/login";
    }
  }
});

export default router;
